import {getDetailList} from '../../api/index';

let state = {
    info: {},   // 
    currentYear: '',// 
    obj:{
        CoverPhoto:'',
        pic_group_count:'',
        dealer_price:'',
        official_refer_price:'',
        SerialID:''
    }
}
let getters = {
    years(state){
        if (!state.info.list){
            return [];
        }
        let arr = state.info.list.map(item=>{
            return item.market_attribute.year;
        })
        let set = new Set(arr);
        return ['',...set];
    },
    list(state){
        if (!state.info.list){
            return [];
        }
        // 
        function merge(arr){
            let types = [];
            let obj = {};
            console.log('arr..', arr);
            arr.forEach(item=>{
                let type = item.exhaust_str+'/'+item.max_power_str+' '+item.inhale_type;
                if (types.indexOf(type) == -1){
                    types.push(type);
                    obj[type] = [item];
                }else{
                    obj[type].push(item);
                }
            })
            return obj;
        }

        if (state.currentYear == ''){
            return merge(state.info.list)
        }else{
            return merge(state.info.list.filter(item=>{
                return item.market_attribute.year == state.currentYear;
            }))
        }
    }
}
let mutations = {
    // 
    changeYear(state, payload){
        state.currentYear = payload;
    },
    // 
    updateInfo(state, payload){
        console.log(',,,,,,,,,,,,,,,,,,,,,,,,',payload)
        // 
        payload.list.sort((a, b)=>{
            //  ,
            if (b.market_attribute.year != a.market_attribute.year){
                return b.market_attribute.year - a.market_attribute.year;
            }else{
                //  
                if (b.exhaust_str > a.exhaust_str){
                    return -1;
                }else if(b.exhaust_str < a.exhaust_str){
                    return 1;
                }else{
                    //  
                    if (b.max_power_str > a.max_power_str){
                        return -1;
                    }else if(b.max_power_str < a.max_power_str){
                        return 1;
                    }else{
                        //  
                        if (b.market_attribute.dealer_price_min > a.market_attribute.dealer_price_min){
                            return -1;
                        }else{
                            return 1
                        }
                    }
                }
            }
        })
        console.log(payload.list);
        state.info = payload;
        state.obj.CoverPhoto = payload.CoverPhoto;
        state.obj.SerialID = payload.SerialID;
        state.obj.pic_group_count = payload.pic_group_count;
        state.obj.dealer_price = payload.market_attribute.dealer_price;
        state.obj.official_refer_price = payload.market_attribute.official_refer_price
    }
}

let actions = {
    getDetailList({commit}, payload){
        getDetailList(payload)
        .then(body=>{
            console.log('detail list...', body);
            commit('updateInfo', body.data);
        })
    }
}

export default{
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}



