/**
 * 
 * @param carId         id
 * @param mobile        
 * @param dealerIds     id
 * @param location      
 * @param carname       
 * @param locationid    id
 * @param name          
 * @param openUDID      
 * @param flag          1
 * @param orderTypeId   1
 * @param channelId     0
 * @return promise      pomise
 */
export let submit = (info)=>{
    let search = ``;
    for (let item in info){
        search += `&${item}=${info[item]}`;
    }
    search[0] = '?'
    return sendRequest(`${host}/h2-submit-lowprice.html${search}`);
}



